import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class TodoItem extends Component {
  getStyle = () =>{

      return{
        background:'#f4f4f4',
        padding: '10px',
        borderBottom: '1px #ccc dotted',
        textDecoration: this.props.todo.completed ? 'line-through' :
        'none'
      }
    }

  render() {
    const { id, title } = this.props.todo;
    return (
      <div style={this.getStyle()}>

      <p>
      <input type="checkbox" className='mr-2' onChange={this.props.markComplete.bind
      (this, id)}/>{''}
      { title }
      <button onClick={this.props.delTodo.bind(this, id)} className= 'btn btn-sm btn-danger ml-2 float-right'>x</button>
      </p>

      </div>


    );
  }
}

//proptypes
TodoItem.propTypes = {
  todo: PropTypes.object.isRequired,
  markComplete: PropTypes.func.isRequired,
  delTodo: PropTypes.func.isRequired,

}



